/*Estilos de fuente:*/

@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap');

/*Configuración del header (contenedor)*/

.container-header{
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 50px;
    border-bottom:50px solid rgb(230, 230, 230);
}

/*Configuración del menú*/

.container-menu{
    width: 80%;
    position: absolute;
    left: 0;
    right: 0;
    top: 140px;
    margin: auto;
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    float: left;
    list-style: none;
    background-color: #5ab507;
}

.container-menu .item-menu a{
    padding-right: 25px;
    padding-left: 25px;
    font-family: 'Titillium Web', sans-serif;
    text-decoration: none;
    font-size: 18px;
    color: #fff;
}

.container-menu .item-menu a:hover{
    color: #2956b2;
    text-decoration: underline;
}

/*Configuración del logo*/

.container-logo{
    width: 100px;
    height: 100px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    margin-right: 5px;
    background-color: #5ab507;
}

.logo-item{
    font-size: 40px;
    color: #fff;
}

.container-title{
    display: flex;
    align-items: center;
}

.title{
    font-family: 'Raleway';
    font-size: 28px;
    color: #2956b2;
}

/*Bloqueo configuración menú vértical para móviles*/

.container-logo-movil{
    display: none;
}

.container-button-menu{
    display: none;
}

.container-menu-movil, .invisible{
    display: none;
}

/*Configuración botón flotante*/

    .btn-float{
        position: fixed;
        margin-top: 800px;
        right: 10px;
    }

.button-float{
    font-size: 50px;
    color: rgb(126, 122, 122);
}

.button-float:hover{
    font-size: 60px;
    color: #5ab507;
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Configuración del header (contenedor)*/

.container-header{
    background-color: #fff;
    padding-top: 30px;
    padding-bottom: 30px;
    border-bottom: none;
    position: fixed;
}

/*Bloqueo configuración menú horizontal para computadoras*/

.container-menu{
    display: none;
}

.container-logo{
    display: none;
}

.container-title{
    display: none;
}

/*Configuración del logo*/

.container-logo-movil{
    width: 50px;
    height: 50px;
    border-radius: 100%;
    position: absolute;
    right: 10px;
    top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #5ab507;
}

.logo-item-movil{
    font-size: 25px;
    color: #fff;
}

/*Configuración menú vertical para móviles*/

.container-menu-movil{
    width: 50%;
    display: flex;
    flex-direction: column;
    position: absolute;
    transform: scaleY(1);
    transform-origin: top;
    transition: 0.3s;
    left: 15px;
    top: 45px;
}

/*Configuración especial para hacer visible el menú*/

.invisible{
    width: 50%;
    display: flex;
    flex-direction: column;
    position: absolute;
    transform: scaleY(0);
    transform-origin: top;
    transition: 0.3s;
    left: 15px;
    top: 45px;
}

.invisible button a{
    font-family: 'Titillium Web', sans-serif;
    text-decoration: none;
    font-size: 18px;
    color: #fff;
}

/*------------------------------------------------------*/

.option-menu{
    border:none;
    text-align: left;
    background-color: #2956b2;
    padding: 15px 10px;
}

.container-menu-movil button a{
    font-family: 'Titillium Web', sans-serif;
    text-decoration: none;
    font-size: 18px;
    color: #fff;
}

.container-button-menu{
    width: 10%;
    position: absolute;
    border: none;
    background-color: #fff;
    top: 10px;
    left: 10px;
    display: flex;
}

.button-menu{
    font-size: 35px;
    color: #5ab507;
    cursor: pointer;
}

/*Configuración botón flotante*/

.button-float{
    display: none;
}

} /*Final del media query # 1*/

/*-------------------------Media Query min-width: 550px-------------------------*/

@media (min-width: 550px){

.container-menu{
    width: 94%;
}

.container-menu .item-menu a{
    padding-right: 15px;
}

.btn-float{
    margin-top: 175px;
}
    
}

/*-------------------------Media Query min-width: 720px-------------------------*/

@media (min-width: 720px){

.container-menu{
    width: 80%;
}

.container-menu .item-menu a{
    padding-right: 25px;
    padding-left: 25px;
}

.btn-float{
    margin-top: 450px;
}

}

/*-------------------------Media Query min-width: 1280px-------------------------*/

@media (min-width: 1280px){

.title{
    font-size: 50px;
}

.container-menu{
    width: 80%;
}

.container-menu .item-menu a{
    padding-right: 30px;
    padding-left: 30px;
    font-size: 25px;
}

.btn-float{
    margin-top: 800px;
}
    
}